


























import Vue from "vue";
import { mapMutations, mapState } from "vuex";
import { ROOMS } from "../../consts";
export default Vue.extend({
  data: () => ({
    rooms: ROOMS,
  }),
  computed: {
    ...mapState(["lights"]),
  },
  methods: {
    ...mapMutations(["onOffLight"]),
  },
});
